import Vue from "vue";
 import VueRouter from "vue-router";
 Vue.use(VueRouter)
 import Index from "./views/index.vue";
 import Home from "./views/home.vue";
 import Login from "./views/login.vue";
 import Register from './views/register.vue';
 import Myinfo from './views/myinfo.vue';
 import App from './views/app.vue';
 import GoodsList from './views/goodslist.vue';
 import GoodsDetail from './views/goodsdetail.vue';
 import MyShoppingCar from './views/myshoppingcar.vue';
 import GoodSearch from './views/goodsearch.vue';
 import AddRess from './views/address.vue';
 import ChecKdan from './views/checkdan.vue'
 const routes =[
     {
         path:"",
         name:"index",
         component:Index
     },
     {
         path:"/app",
         name:'app',
         component:App,
         children:[
             {path:"",redirect:{name:'home'}},
             {path:"home",name:"home",component:Home},
             {path:"goodslist",name:"goodslist",component:GoodsList},
             {path:"myshoppingcar",name:"myshoppingcar",component:MyShoppingCar},
             {path:"myinfo",name:'myinfo',component:Myinfo},
             {path:"*",redirect:{name:'home'}}
         ]
     }
    ,
    {
        path:"/address",
        name:"address",
        component:AddRess
    },
    {
        path:"/checkdan",
        name:"checkdan",
        component:ChecKdan
    },
    {
        path:"/goodsdetail",
        name:"goodsdetail",
        component:GoodsDetail
    } ,
    {
        path:"/goodsearch",
        name:"goodsearch",
        component:GoodSearch
    },
    {
        path:"/login",
        name:"login",
        component:Login   
    },
    {
        path:"/register",
        name:"register",
        component:Register   
    }
    ,
    {
        path:"*",
        path:"home",
        component:Home
    }
 ]
 const router = new VueRouter({
     routes,
     mode:"hash"
 })
 export default router;


