<template>
  <div>
    <mt-header fixed title="分类">
      <router-link :to="{name:'goodsearch'}" slot="right">
        <mt-button icon="search"></mt-button>
      </router-link>
    </mt-header>
    <div>
      <div class="goods-li">
        <div
          v-for="(item,i) in types"
          :class="tabId==i?'goods-til':''"
          @click="showGoods(item,i)"
          :key="i"
        >{{item}}</div>
      </div>

      <div class="goodss-ino">
        <div class="good-ino moveIn" v-for="(item,i) in goodsNum" :key="i">
          <router-link
            :to="{name:'goodsdetail',query:{goodsid:item.goodsid,goodsimg:item.goodsimg}}"
          >
            <div class="fen-goods">
              <img v-lazy="require('../../assets/image/'+item.goodsimg)" alt>
            </div>
          </router-link>
          <div class="title-goods">
            <p class="goods-name">{{item.goodsname}}</p>
            <p class="goods-num">{{item.goodscount}}</p>
            <p class="mong-cou">
              ￥{{item.goodsprice}}
              <span class="car-add iconfont" @click="addOneShoppings(item)">&#xf0179;</span>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from "mint-ui";
export default {
  data() {
    return {
      goodsToal: [],
      goodsNum: [],
      types: [],
      tabId: 0
    };
  },
  created() {
    this.$http.get("http://39.97.98.141:3700/vue/goodsList").then(res => {
      this.types = res.body.type;
      this.goodsToal = res.body.goodslist;
      this.goodsNum = res.body.goodslist.filter(
        (item, index) => item.type == this.types[0]
      );
    });
  },
  methods: {
    showGoods(namegoods, mun) {
      this.tabId = mun;
      this.goodsNum = this.goodsToal.filter(
        (item, index) => item.type == namegoods
      );
    },
    addOneShoppings(goodsino) {
      if (localStorage.username) {
        this.$store.dispatch("addOneShoppings", {
          url: "http://39.97.98.141:3700/vue/buyshoppings",
          shopinfo: {
            goodsinfo:goodsino,
            username: localStorage.username
          },
          cb: result => {
            if (result) {         
              Toast({
                message: result.msg,
                duration: 500
              });
            }
          }
        });
      } else {
        Toast({
          message: "您还没有登录！",
          duration: 1000
        });
      }
    }
  }
};
</script>
<style lang="css" scoped>
.mint-header {
  background: #01a84a;
  color: #fff;
}
.goods-til {
  background-color: #fff;
  color: #01a84a;
}
.goods-li {
  position: fixed;
  width: 18%;
  top: 90px;
  font-size: 26px;
  background-color: #f7f7f7;
  color: #000;
  left: 0px;
  line-height: 120px;
  text-align: center;
}

.good-ino {
  overflow: hidden;
  width: 80%;
  margin-top: 100px;
  float: right;
  margin-left: 40px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  line-height: 100px;
}
.fen-goods {
  float: left;
  width: 44%;
  height: 300px;
}
.fen-goods img {
  width: 200px;
  height: 250px;
}
.title-goods {
  width: 55%;
  float: right;
}
.goods-name {
  font-size: 28px;
  line-height: 60px;
  margin-left: 10px;
}
.goods-num {
  line-height: 60px;
  font-size: 12px;
  color: rgb(194, 187, 187);
}
.mong-cou {
  margin-top: 45px;
  line-height: 60px;
  color: orange;
}
.mong-cou .car-add {
  display: inline-block;
  width: 56px;
  height: 56px;
  line-height: 56px;
  text-align: center;
  border-radius: 50%;
  margin-left: 140px;
  color: #fff;
  background-color: #01a84a;
}
</style>




