<template>
    <footer>
      <div v-for="(foot,i) in foots" :key="i">
          <router-link :to="{name:foot.name}">
						<mt-badge class="hot" type="warning" v-show="flagess" v-if="i==2" > {{buygoodsnum}}</mt-badge>
          <i class="iconfont" v-html="foot.icon"></i>
          <span>{{foot.txt}}</span>

          </router-link>
      </div>
    </footer>
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
  computed:{
    ...mapState(["foots",'buygoodsnum','flagess'])
  },
	created() {
		if(localStorage.username){
		  this.$store.dispatch("showShoppingNum");
		}
	}
    
}
</script>
<style lang="scss" scoped>
footer{
	position:fixed;
	width:100%;
	height:90px;
	left:0;
	bottom:0;
	background:#fff;
	display:flex !important;
    z-index:100;
    border-top:1px solid rgb(235, 231, 231);
	div{
		flex:1;
		height:90px;
		a{
			display: block;
			width:100%;
			height:100%;
			display:flex;
			flex-direction:column;
			box-sizing:border-box;
			padding:6px 0;
			text-align:center;
			font-family:"微软雅黑";
			color:#999;
			position: relative;
			i{
			width:100%;
			height:45px;
			line-height:45px;
			font-size:42px !important;
			}
			span{
				width:100%;
				height:35px;
				line-height:35px;
				font-size:26px;
			}
			.hot{
				position: absolute;
				top:0px;
				font-size: 12px;
				left:58%;
			background-color: rgb(236, 69, 69);
				text-align: center;
				display: inline;
				width:60px;
			}
		}
.mint-badge.is-size-normal {
    border-radius: 12px;
    font-size: 12px;
    padding: 1px 4px;
}
		.nav-active,.router-link-exact-active{
			color:#01a84a;
				border-color:#01a84a;
		}

	}
}
</style>




