<template>
  <div class="move-enter-active"> 
      <div id="tui">广告</div>
    <div id="closeds" @click="closed">{{num}}秒 跳过</div>
    <img :src="inImg" class="img-item">
  </div>
</template>
<script>
import router from "../router";
import {mapState,mapActions,mapGetters} from "vuex";
export default {
  mounted() {
   this.$store.dispatch("countDesc") 
 },
  methods: {
    closed() {
        this.$router.push({name:"home"})
    }
  },
  computed:{
    ...mapState(['num','inImg']) ,
  }
  ,
  watch:{
      num:{
             handler(newVal){
        if(newVal==0)    {
          this.$router.push({name:"home"})   
          this.$store.commit("rescNum");
        }},
      deep:true
                }
  }
};
</script>
<style lang="css" scoped>
.img-item {
  width: 100%;
  height: 100%;
}
#closeds ,#tui {
  background: rgba(0, 0, 0, 0.1);
  text-align: center;
  position: absolute;
}
#closeds{
     border-radius: 30px 30px ;
    font-size: 18px;
 line-height: 70px;
  width: 140px;
  height: 70px;
  bottom: 10%;
  right: 20%;
}
#tui{
  width: 60px;
  height: 50px;
  line-height: 50px;
  font-size:11px;
  top: 5%;
  right: 2%;
}
.move-enter-active{
    animate:zoomIn 3s;
}
.move-leave-active{
    animate:zoomOut 3s;
}
</style>




