<template>
  <div>
    <mt-header fixed slot="left" title="配送至： 武汉市">
      <router-link :to="{name:'goodsearch'}" slot="right">
        <mt-button icon="search"></mt-button>
      </router-link>
      <router-link :to="{name:'myinfo'}" slot="right" style="margin-left:10px">
        <mt-button>
          <span class="iconfont">&#xe6ac;</span>
        </mt-button>
      </router-link>
    </mt-header>
    <div>
      <div class="lunbo">
        <mt-swipe :continuous="true" :auto="1200" style="height:200px;width:100%">
          <mt-swipe-item v-for="(item,i) in imgs" :key="i">
            <router-link :to="{name:'goodslist'}">
            <img :src="item" alt class="img-item">
            </router-link>
          </mt-swipe-item>
        </mt-swipe>
      </div>
      <div class="img-ise" v-for="(im , i) in imgse" :key="i">
        <router-link :to="{name:'goodslist'}">
          <img :src="im" alt>
        </router-link>
      </div>
      <div class="goods-jie">
        <ul>
          <router-link :to="{name:'goodsdetail',query:{goodsid:'1002',goodsimg:'kun.png'}}">
            <li>
              <img src="../../assets/image/kun.png" alt>
              <p>B级-进口恐龙蛋李</p>
            </li>
          </router-link>
          <router-link :to="{name:'goodsdetail',query:{goodsid:'1004',goodsimg:'on2.jpg'}}">
            <li>
              <img src="../../assets/image/on2.jpg" alt>
              <p>奶油草莓</p>
            </li>
          </router-link>
          <router-link :to="{name:'goodsdetail',query:{goodsid:'1006',goodsimg:'xiu1.png'}}">
            <li>
              <img src="../../assets/image/xiu1.png" alt>
              <p>罐装原味瓜子218g</p>
            </li>
          </router-link>
          <router-link :to="{name:'goodsdetail',query:{goodsid:'1005',goodsimg:'tw3.jpg'}}">
            <li>
              <img src="../../assets/image/tw3.jpg" alt>
              <p>【82折】柑橘</p>
            </li>
          </router-link>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapActions, mapGetters } from "vuex";
export default {
  computed: {
    ...mapState(["imgs", "imgse"])
  }
};
</script>
<style lang="scss" scoped>
.mint-header {
  background: #01a84a;
  color: #fff;
}
.goods-jie a{
  color: #000;
}
.img-item {
  overflow: hidden;
  width: 100%;
  height: 100%;
}
.lunbo{margin-top: 80px;}
.img-ise {
  margin: 10px auto;
  width: 98%;
}
.img-ise img {
  width: 100%;
  height: 200px;
}
.goods-jie ul {
  margin-bottom: 100px;
  width: 98%;
  margin-left: 2%;
  overflow: hidden;
}
.goods-jie ul li {
  float: left;
  width: 46%;
  margin: 1%;
}
.goods-jie ul li img {
  width: 100%;
  height: 200px;
}
.goods-jie ul li p {
  line-height: 60px;
  font-size: 20px;
  text-align: center;
}
</style>



