<template>
  <div>
    <span class="go-back iconfont" @click="goBtn">&#xe64b;</span>
    <router-link :to="{name:'home'}">
      <span class="go-home iconfont">&#xe62e;</span>
    </router-link>
    <div id="shop-info">
      <div class="img-info">
        <img :src="imgSrc" class="img-item">
      </div>
      <p class="goods-name">
        {{goodsDetail.goodsname}}
        <span class="fen-coun">{{goodsDetail.goodsnum}}积分</span>
      </p>
      <p class="tiles">今日宜奢逸，一口一个黑珍珠</p>
      <p class="goods-num">{{goodsDetail.goodscount}}</p>
      <p class="mong-cou">￥{{goodsDetail.goodsprice}}</p>
    </div>
    <div id="goods-footer">
      <router-link :to="{name:'myshoppingcar'}">
        <div class="shop-add" :class="flds?'moveIn':''">
          购物车(
          <span>{{buygoodsnum}}</span>)
        </div>
      </router-link>
      <span class="shop-car" @click="addGoods">加入购物车</span>
    </div>
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
import { MessageBox ,Toast} from "mint-ui";
export default {
  data() {
    return {
      imgSrc: "",
      flds:false
    };
  },
  created() {
    if (this.$route.query.goodsid) {
      this.imgSrc = require("../../assets/image/" + this.$route.query.goodsimg);
      this.$store.dispatch("showShoppingNum");
      this.getGoods({
        url: "http://39.97.98.141:3700/vue/goodsDetail",
        goodsid: this.$route.query.goodsid
      });
    } else {
      this.$router.push({ name: "home" });
    }
  },
  computed: {
    ...mapState(["goodsDetail", "buygoodsnum"])
  },
  methods: {
    ...mapActions(["getGoods"]),
    goBtn() {
      this.$router.go(-1);
    },
    addGoods() {
       this.flds=false;
      if (localStorage.username) {
        this.$store.dispatch("addOneShoppings", {
          url: "http://39.97.98.141:3700/vue/buyshoppings",
          shopinfo: {
            goodsinfo: this.goodsDetail,
            username: localStorage.username
          },
          cb: result => {
            if (result) {   
               this.flds=true;      
              Toast({
                message: result.msg,
                duration: 500
              });
            }
          }
        });
      } else {

  MessageBox.confirm('',{
        title: "提示",
        message: "您还没有登录,去登陆?",
        showCancelButton: true
      }).then(action => {
             if (action == 'confirm') {
              this.$router.push({name:"login"})
          }
        }).catch(err => {
          if (err == 'cancel') {
          }
       })
      }
    }
  }
};
</script>
<style lang="css" scoped>
.mint-header {
  background: #fff;
  color: #000;
}
#shop-info {
  margin-bottom: 100px;
}
.goods-name {
  line-height: 80px;
  margin-left: 10px;
}
.tiles {
  line-height: 60px;
  margin-left: 20px;
  font-size: 30px;
  letter-spacing: 4px;
}
.goods-num {
  line-height: 60px;
  font-size: 12px;
  margin-left: 20px;
}
.mong-cou {
  line-height: 80px;
  color: orange;
  margin-left: 20px;
}
.fen-coun {
  float: right;
  margin-right: 10px;
  color: orange;
}
.go-back,
.go-home {
  background: rgba(0, 0, 0, 0.1);
  text-align: center;
  position: fixed;
  color: #fff;
  width: 50px;
  line-height: 50px;
  height: 50px;
  border-radius: 50%;
}
.go-home {
  top: 15px;
  right: 15px;
}
.go-back {
  top: 15px;
  left: 15px;
}
.img-item {
  width: 100%;
  height: 800px;
}
#goods-footer {
  border-top: 2px solid red;
  width: 100%;
  background-color: #fff;
  z-index: 1000;
  height: 100px;
  line-height: 100px;
  bottom: 0px;
  position: fixed;
}
#goods-footer .shop-car {
  border-top: 2px solid red;
  width: 50%;
  text-align: center;
  color: #fff;
  font-size: 16px;
  letter-spacing: 3px;
  background-color: orange;
  float: right;
}
#goods-footer .shop-add {
  border-top: 2px solid red;
  width: 50%;
  text-align: center;
  color: #000;
  font-size: 16px;
  letter-spacing: 3px;
  background-color: #fff;
  float: left;
}
.shop-add span {
  color: orange;
}
</style>





