<template>
  <div>
    <mt-header title="账户登录">
      <router-link :to="{name:'home'}" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div class="login-info">
      <div class="login-text">
        <mt-field label="用户名" placeholder="请输入用户名" v-model="user.username"></mt-field>
        <mt-field  label="密码"   placeholder="请输入密码"   type="password"   v-model="user.password" ></mt-field>
      </div>
      <div class="login-btn">
        <mt-button type="danger" size="large" :class="user.username&&user.password?'':btn.dise"   @click="loginBtn">登录</mt-button>
      </div>
      <div class="regist-btn">
        <router-link :to="{name:'register'}">
          <button class="mint-button mint-button--danger mint-button--small is-plain">
            <label class="mint-button-text">免费注册</label>
          </button>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
import { Toast } from "mint-ui";
export default {
  methods: {
    loginBtn() {
      this.$store.dispatch("loginBtn", {
        url: "http://39.97.98.141:3700/vue/login",
         user:this.user ,
        cb: nes => {
         if(nes.code==200){
                Toast({
                  message: nes.msg,
                  duration: 500
                });
                localStorage.username=this.user.username;
                this.$router.push({name:"home"})
            }else if(nes.code=400){
          Toast({
                  message: nes.msg,
                  duration: 500
                  })
            }
        }
      });
    }
  },
  computed: {
    ...mapState(["user","btn"])
  },
  mounteds() {}
};
</script>
<style lang="css" scoped>
.login-info {
  margin: auto;
  width: 96%;
}
.login-text {
  margin-bottom: 4%;
}
.regist-btn {
  margin-top: 10%;
  text-align: center;
}
.mint-header {
  background: #01a84a;
  color: #fff;
}
</style>





