<template>
  <div>
    <mt-header title="注册">
      <router-link :to="{name:'login'}" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div class="register-info">
      <div class="register-text">
        <mt-field label="用户名" placeholder="含字符、数字长度4~8" v-model="user1.username"></mt-field>
        <mt-field label="手机号码" placeholder="请输入手机号码" type="tel" v-model="user1.tel"></mt-field>
        <mt-field
          label="设置密码"
          placeholder="含字符、数字长度6~8"
          type="password"
          v-model="user1.password"
        ></mt-field>
        <mt-field label="确认密码" placeholder="再次输入密码" type="password" v-model="user1.password1"></mt-field>
      </div>
      <div class="register-btn">
        <mt-button
          type="danger"
          size="large"
          :class="user1.username&&user1.password&&user1.tel&&user1.password1?'':btn.dise"
          @click="regBtn"
        >同意协议并注册</mt-button>
      </div>
      <div class="register-stack">
        <p class="stack-til">已阅读并同意以下协议</p>
        <p class="stack-nei">鲜果平台服务协议、隐私权政策、法律声明、支付宝服务协议</p>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
import { Toast } from "mint-ui";
export default {
  methods: {
    regBtn() {
      if (this.reg.nameReg.test(this.user1.username)) {
        this.btn.falge = true;
      } else {
        Toast({
          message: "用户名无效",
          duration: 500
        });
      }
      if (this.btn.falge) {
        if (this.reg.telReg.test(this.user1.tel)) {
          this.btn.falge = true;
          if (this.reg.pwdReg.test(this.user1.password)) {
            if (this.user1.password === this.user1.password1) {
              this.btn.falge = true;
            } else {
              this.btn.falge = false;
               Toast({
              message: "两次密码不一致",
              duration: 500
            });
            }
          } else {
            this.btn.falge = false;
            Toast({
              message: "请输入有效密码",
              duration: 500
            });
          }
        } else {
          this.btn.falge = false;
          Toast({
            message: "请输入有效手机号",
            duration: 500
          });
        }
      }

      if (this.btn.falge) {
        this.$store.dispatch("regBtn", {
          url: "http://39.97.98.141:3700/vue/register",
          user1: this.user1,
          cb: nes => {
            if (nes.code == 200) {
              Toast({
                message: nes.msg,
                duration: 1000
              });
              this.$router.push({ name: "login" });
            } else if ((nes.code = 300)) {
              Toast({
                message: nes.msg,
                duration: 1000
              });
            }
          }
        });
      }
    }
  },
  computed: {
    ...mapState(["user1", "btn", "reg"])
  }
};
</script>
<style lang="css" scoped>
.register-info {
  margin: auto;
  width: 96%;
}
.register-text {
  margin-bottom: 4%;
  font-size: 16px;
}
.register-stack {
  margin-top: 10%;
  text-align: left;
  font-size: 14px;
  line-height: 60px;
}
.stack-til {
  color: rgb(194, 188, 188);
}
.mint-header {
  background: #01a84a;
  color: #fff;
}
</style>





