<template>
  <div>
    <mt-header>
      <mt-button slot="left" @click="exitBtn">
        <span class="iconfont">&#xe65d;</span>
      </mt-button>
      <mt-button icon slot="right">
        <span class="iconfont">&#xe699;</span>
      </mt-button>
    </mt-header>
    <div>
      <div class="user-ins">
        <div class="img-use">
        <img src="../../assets/image/tou.png" alt="">
        </div>
        <p class="nmef">{{username}}</p>
        <div class="info-ge">
          <ul>
            <li>
              <p>0</p>
              <p>积分商城</p>
            </li>
            <li>
              <p>0</p>
              <p>优惠券</p>
            </li>
            <li>
              <p>0.00</p>
              <p>钱包</p>
            </li>
          </ul>
        </div>
      </div>
    <div class="my-shopcar">
      <p class="my-tiii"> <span class="left-ti">我的订单</span> <span class="right-ti iconfont">全部订单 &#xe600;</span></p>
     <div class="my-ding">
      <ul>
        <li>
           <span class="iconfont">&#xe507;</span>
           <p>待付款</p>
        </li>
          <li>
           <span class="iconfont">&#xe615;</span>
           <p>备货中</p>
        </li>
          <li>
           <span class="iconfont">&#xe6f3;</span>
           <p>配送</p>
        </li>
          <li>
           <span class="iconfont">&#xe60b;</span>
           <p>待评价</p>
        </li>
         <li>
           <span class="iconfont">&#xe699;</span>
           <p>人工服务</p>
        </li>
          <li>
           <span class="iconfont">&#xe628;</span>
           <p>配送</p>
        </li>
          <li>
           <span class="iconfont">&#xe60a;</span>
           <p>邀请 (0)</p>
        </li>
          <li>
           <span class="iconfont">&#xe608;</span>
           <p>待付款</p>
        </li>
      </ul>

     </div>
    </div>
    <div class="footer-myin">
          <img src="../../assets/image/ger.png" alt="">
    </div>
    </div>
  </div>
</template>
<script>
import { MessageBox } from "mint-ui";
export default {

  beforeCreate() {
    if (!localStorage.username) {
      this.$router.push({ name: "login" });
    }else{
           this.username=localStorage.username;
    }
  },
  
  methods: {
    exitBtn() {
      MessageBox.confirm('',{
        title: "提示",
        message: "确定注销此账号?",
        showCancelButton: true
      }).then(action => {
             if (action == 'confirm') {
            delete  localStorage.username;
              this.$router.push({name:"home"})
            window.location.reload();
          }
        }).catch(err => {
          if (err == 'cancel') {
          }
       })
         }
  },
  computed: {
    username : {
    get () {
      return this.$store.state.username
    },
    set (value) {
      this.$store.commit('updateUsername', value)
    }
  },

  }
};
</script>
<style lang="css" scoped>
.mint-header {
  background: #01b04e;
  color: #fff;
}
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
.user-ins{
  margin: 10px;
  width: 90%;
   background: #01b04e;
  overflow: hidden;
  margin:  auto;
  padding: 0 5% 20px;
}
.user-ins .img-use{
  margin: 10px;
  float: left;
}
.user-ins .img-use img{
  width: 100px;
  height: 100px;
}
.user-ins .nmef{
  float: right;
  color: #fff;
  margin-right: 60%;
  line-height: 100px;
}
.info-ge ul {
  width: 100%;
  overflow: hidden;
}
.info-ge ul li{
  width: 30%;
  float: left;
}
.info-ge ul li p{
font-size: 16px;
text-align: center;
  color: #fff;
  width: 100%;
  height: 50px;
  line-height: 50px;
}
.my-tiii{
  line-height: 100px;
  font-size: 30px;
  text-align: center;
  border-bottom: 1px solid #949090;
}
.left-ti{
  float: left;
  margin-left: 30px;
}
.right-ti{
  color: #949090;
  margin-right: 30px;
 float: right;
}
.my-shopcar{
  padding: 50px 0;
}
.my-ding ul{
  width: 100%;
  overflow: hidden;
}
.my-ding ul li{
  text-align: center;height: 200px;
   width: 20%;
  float: left; 
}
.my-ding ul li p{
 line-height: 100px;
 font-size: 20px;
}
.my-ding ul li span{
  color: #01b04e;
}
.footer-myin{
  background-color: #fff;
  width: 90%;
  height: 240px;
  text-align: center;
   max-width: auto;
}
.footer-myin img{
  vertical-align: middle;
  margin-top: 20px;
  margin-left: 5.5%;
 width: 100%;
  height: 180px;
}
</style>



