<template>
  <div>
    <mt-header title="添加地址">
      <router-link :to="{name:'checkdan'}" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div>
      <div class="address-text" >
        <mt-field label="收货人" placeholder="请输入收货人姓名" v-model="ressInfo.username"></mt-field>
        <mt-field label="联系电话" placeholder="请输入收货人姓名" type="tel" v-model="ressInfo.tel"></mt-field>
        <p>所在地区：{{myAddressProvince}} {{myAddressCity}} {{myAddresscounty}}</p>
        <mt-field placeholder="请填写详细地址信息" type="textarea" rows="4" v-model="ressInfo.textareas"></mt-field>
      </div>
    </div>
    <mt-picker :slots="myAddressSlots" @change="onMyAddressChange"></mt-picker>
    <div class="addFoot" @click="updateAddRess">保存</div>
  </div>
</template>
<script>
import { Picker,Toast } from "mint-ui";
import { mapState, mapActions } from "vuex";
import myaddress from "../components/address3.json";
export default {
  components: {
    "mt-picker": Picker
  },
  props: {},
  data() {
    return {
      ressInfo: {},
      isShowAddress: false,
      myAddressSlots: [
        {
          flex: 1,
          defaultIndex: 1,
          values: Object.keys(myaddress), //省份数组
          className: "slot1",
          textAlign: "center"
        },
        {
          divider: true,
          content: "-",
          className: "slot2"
        },
        {
          flex: 1,
          values: [],
          className: "slot3",
          textAlign: "center"
        },
        {
          divider: true,
          content: "-",
          className: "slot4"
        },
        {
          flex: 1,
          values: [],
          className: "slot5",
          textAlign: "center"
        }
      ],
      myAddressProvince: "",
      myAddressCity: "",
      myAddresscounty: ""
    };
  },
  created() {},
    computed: {
    ...mapState(["reg"])
  },
  methods: {
    updateAddRess() {
       if (this.reg.telReg.test( this.ressInfo.tel)) {
      this.$http.get("http://39.97.98.141:3700/vue/addRess", {
        params: {
          username: localStorage.username,
          userinfo: this.ressInfo,
          address:
            this.myAddressProvince + this.myAddressCity + this.myAddresscounty
        }
      }).then(res => {
         Toast({
                  message: res.body.msg,
                  duration: 500
                  });
                  this.$router.push({name:'checkdan'})
      });
      }  else{
          Toast({
            message: "请输入有效手机号",
            duration: 500
          });
      }
    },
    goBack() {
      this.$router.go(-1);
    },
    closeShowAddress(e) {
      if (e.target == this.$refs.selectAddress) {
        this.isShowAddress = !this.isShowAddress;
      }
    },
    onMyAddressChange(picker, values) {
      if (myaddress[values[0]]) {
        //这个判断类似于v-if的效果（可以不加，但是vue会报错，很不爽）
        picker.setSlotValues(1, Object.keys(myaddress[values[0]])); // Object.keys()会返回一个数组，当前省的数组
        picker.setSlotValues(2, myaddress[values[0]][values[1]]); // 区/县数据就是一个数组
        this.myAddressProvince = values[0];
        this.myAddressCity = values[1];
        this.myAddresscounty = values[2];
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.myAddressSlots[0].defaultIndex = 0; // 这里的值需要和 data里面 defaultIndex 的值不一样才能够初始化 //因为我没有看过源码（我猜测是因为数据没有改变，不会触发更新）
    });
  }
};
</script>
<style lang="css" scoped>
.mint-header {
  background: #01a84a;
  color: #fff;
}
.addFoot {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background-color: orange;
  color: #fff;
  line-height: 100px;
  font-size: 16px;
  text-align: center;
}
.address-text p {
  line-height: 100px;
  border-top: 1px solid rgb(207, 202, 202);
}
</style>




