<template>
  <div>
    <div id="kuo">
      <mt-button @click="goBack" icon="back"></mt-button>
      <input type="text" v-model="namesd" id="btn-search">
      <mt-button icon="search"  @click="searchs"></mt-button>
    </div>
     <div class="good-ino" v-for="(item,i) in searchGoods" :key="i">
      <router-link :to="{name:'goodsdetail',query:{goodsid:item.goodsid,goodsimg:item.goodsimg}}">
        <div class="fen-goods moveIn">
          <img v-lazy="require('../../assets/image/'+item.goodsimg)" alt>
        </div>
      </router-link>
      <div class="title-goods">
        <p class="goods-name">{{item.goodsname}}</p>
        <p class="goods-num">{{item.goodscount}}</p>
        <p class="mong-cou">
          ￥{{item.goodsprice}} 
          <span class="car-add iconfont" @click="addOneShoppings(item)">&#xf0179;</span>
        </p>
      </div>
    </div>
    <router-link :to="{name:'myshoppingcar'}">
    <span class="gouwuc iconfont "  :class="flds?'moveIn':''">&#xf0179; {{buygoodsnum?buygoodsnum:""}}</span>
    </router-link>
  </div>
</template>
<script>
import { Toast } from "mint-ui";
import { mapState, mapActions } from "vuex";
export default {
    data(){
        return{
           namesd:"" ,
           flds:false
        }
    },
     created() {
       if(localStorage.username){
         this.$store.dispatch("showShoppingNum");
       }else{
        this.$store.commit('clearCars')
       }
  },
  methods: {
    searchs() {
          this.$store.dispatch("getGoodsInfo", {
        url: "http://39.97.98.141:3700/vue/goodsearch",
         searchs: this.namesd })
    },
    goBack() {
      this.$router.go(-1);
    },
     addOneShoppings(goodsino) {
         this.flds=false;
      if (localStorage.username) {
        this.$store.dispatch("addOneShoppings", {
          url: "http://39.97.98.141:3700/vue/buyshoppings",
          shopinfo: {
            goodsinfo:goodsino,
            username: localStorage.username
          },
          cb: result => {
            if (result) {   
               this.flds=true     
              Toast({
                message: result.msg,
                duration: 500
              });
            }
          }
        });
      } else {
        Toast({
          message: "您还没有登录！",
          duration: 1000
        });
      }
    }
  },
    computed: {
    ...mapState(['searchGoods','buygoodsnum'])
  },
};
</script>
<style lang="css" scoped>
#kuo {
  position: fixed;
  width: 100%;
  height: 100px;
  background-color: #f6f8fa;
}
#kuo .mint-button{
box-shadow:none;
}
#btn-search {
    margin-top: 10px;
  width: 70%;
  height: 80%;
  border:none;
}
.gouwuc{
 position: fixed;
 left: 10px;
 color: #01a84a;
 bottom: 150px;
 text-align: left;
 font-size: 12px;
 line-height: 60px;
 width: 70px;
 border-radius: 30%;
 height: 60px;
 background:rgba(0, 0, 0, 0.3);
}
.good-ino {
  overflow: hidden;
  width: 98%;
  margin: auto;
  margin-top: 150px;
  float: right;
  margin-left: 40px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  line-height: 100px;
}
.fen-goods {
  float: left;
  width: 44%;
  height: 300px;
}
.fen-goods img {
  width: 300px;
  height: 250px;
}
.title-goods {
  width: 55%;
  float: right;
}
.goods-name {
  font-size: 28px;
  line-height: 60px;
  margin-left: 10px;
}
.goods-num {
  line-height: 60px;
  font-size: 12px;
  color: rgb(194, 187, 187);
}
.mong-cou {
  margin-top: 45px;
  line-height: 60px;
  color: orange;
}
.mong-cou .car-add {
  display: inline-block;
  width: 56px;
  height: 56px;
  line-height: 56px;
  text-align: center;
  border-radius: 50%;
  margin-left: 200px;
  color: #fff;
  background-color: #01a84a;
}
</style>





