<template>
  <div>
    <mt-header fixed title="购物车">
      <mt-button slot="right" @click="delBtn">删除</mt-button>
    </mt-header>
    <div class="car-til">
      <img src="../../assets/image/hui.png" alt>
    </div>
    <div class="blank" v-if="flage">
      <img class="img-1" src="../../assets/image/gpuwu.png">
      <p class="p1">购物车没有商品哦</p>
      <p class="p2" @click="goShopping">去逛逛 >></p>
    </div>
    <div v-else class="xians">
      <ul class="ul-u" v-for="(item,i) in goodstal " :key="i">
        <li class="li-l">
          <input class="checkone" type="checkbox" v-model="item.select">
        </li>
        <li class="li-l">
          <router-link
            :to="{name:'goodsdetail',query:{goodsid:item.goodsid,goodsimg:item.goodsimg}}"
          >
            <img class="li-img" :src="require('../../assets/image/'+item.goodsimg)">
          </router-link>
        </li>
        <li class="li-l">
          <p class="p-p1">
            <span class="font-s">{{item.goodsname}}</span>
          </p>
          <p class="p-p2">
            <span class="sp1">￥{{item.goodsprice}}</span>
            <span class="sp2">
              <i class="i1" @click="item.buynum<=1?item.buynum=1:decGoodsIn(--item.buynum,item)">一</i>
              <input class="i2" style="width:15px;" v-model="item.buynum" readonly>
              <i class="i3" @click="addGoodsIn(++item.buynum,item)">十</i>
            </span>
          </p>
        </li>
      </ul>

      <div class="checkall">
        <ul class="ul-box">
          <li>
            <input type="checkbox" v-model="quan">
          </li>
          <li>全选</li>
          <li>
            <p class="l-p1">
              合计
              <span>￥{{total}}</span>
            </p>
          </li>
          <li>
            <p class="result" @click="goCheck">去结算</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import { MessageBox,Toast } from "mint-ui";
import { mapState, mapActions, mapMutations } from "vuex";
export default {
  data() {
    return {
      flage: false,
      goodstal: []
    };
  },
  created() {
    if (!localStorage.username) {
      this.$router.push({ name: "login" });
    } else {
      this.$http
        .get("http://39.97.98.141:3700/vue/shoppingcar", {
          params: {
            username: localStorage.username
          }
        })
        .then(res => {
          if (res.body.result.length != 0) {
            this.flage = false;
            this.goodstal = res.body.result;
            var _this = this;
            this.goodstal.map(function(item) {
              _this.$set(item, "select", true);
            });
          } else {
            this.flage = true;
          }
        });
    }
  },
  computed: {
    quan: {
      get() {
        var flag = true;
        this.goodstal.forEach(item => {
          if (!item.select) {
            flag = false;
          }
        });
        return flag;
      },
      set(newv) {
        this.goodstal = this.goodstal.map(item => {
          item.select = newv;
          return item;
        });
      }
    },
    total: {
      get() {
        var total = 0;
        var nums = 0;
        this.goodstal.forEach(item => {
          nums += item.buynum * 1;
          if (item.select) {
            total += item.buynum * item.goodsprice * 1;
          }
        });
        this.$store.commit("toatlCarNum", nums);
        return total.toFixed(2);
      }
    }
  },
  watch: {
    goodstal: {
      handler(newVal) {
        this.all = 0;
        newVal.forEach(item => {
          if (item.select) {
            this.all += item.goodsprice * 1 * item.buynum * 1;
          }
        });
      },
      deep: true
    }
  },
  methods: {
    goCheck() {
    var checkgoods=[]
      this.goodstal.forEach(item => {
        if (item.select) {
          checkgoods.push(item.goodsid)
        }
      });
      if(checkgoods.length){
        this.$router.push({ name: "checkdan" });
            localStorage.checkgoods=checkgoods;
      }else{
        Toast({
          message: "请选择商品",
          duration: 500
        });
      }
    },
    delBtn() {
      var delGoods = [];
      MessageBox.confirm("", {
        title: "提示",
        message: "确定删除商品?",
        showCancelButton: true
      })
        .then(action => {
          if (action == "confirm") {
            this.goodstal.forEach(item => {
              if (item.select) {
                delGoods.push(item);
              }
            });
            this.$store.dispatch("delCarInfo", {
              url: "http://39.97.98.141:3700/vue/delInfoCar",
              delGoods,
              cb: result => {
                if (this.goodstal.length == 0) {
                  this.flage = true;
                  location.reload();
                }
              }
            });
          }
          this.goodstal = this.goodstal.filter(good => !good.select);
        })
        .catch(err => {
          if (err == "cancel") {
          }
        });
    },
    goShopping() {
      this.$router.push({ name: "home" });
    },
    addGoodsIn(addNum, goodsInfo) {
      this.$store.dispatch("updateCarInfo", {
        url: "http://39.97.98.141:3700/vue/updateCarInfo",
        totalInfo: { goodsInfo, addNum }
      });
      //  this.$store.dispatch("showShoppingNum");
      // this.$store.commit('toatlCarNum',nums);
    },
    decGoodsIn(addNum, goodsInfo) {
      this.$store.dispatch("updateCarInfo", {
        url: "http://39.97.98.141:3700/vue/updateCarInfo",
        totalInfo: { goodsInfo, addNum }
      });
    }
  },
  mounted() {
    this.goodstal.forEach(item => {
      if (item.select) {
        this.all += item.goodsprice * 1 * item.buynum * 1;
      }
    });
  }
};
</script>
<style lang="css" scoped>
.mint-header {
  background: #01a84a;
  color: #fff;
}
.car-til {
  background-color: #fff;
  width: 90%;
  margin-top: 100px;
  height: 100px;
  text-align: center;
  max-width: auto;
}
.car-til img {
  vertical-align: middle;
  margin-top: 20px;
  margin-left: 5.5%;
  width: 100%;
  height: 76px;
}
.div-d {
  width: 100%;
  height: 100%;
}
.xians {
  margin-bottom: 220px;
}
.ul-u {
  padding: 10px 0;
  border-top: 1px solid rgb(136, 137, 138, 0.5);
  border-bottom: 1px solid rgb(136, 137, 138, 0.5);
  width: 100%;
  height: 198px;
  overflow: hidden;
  text-align: center;
}
.ul-u .li-l {
  height: 100%;
  float: left;
}
.ul-u .li-l:nth-child(1) {
  width: 9%;
  height: 100%;
  overflow: hidden;
}
.checkone {
  margin-top: 90px;
}
.ul-u .li-l:nth-child(2) {
  width: 26%;
  overflow: hidden;
}
.li-img {
  width: 100%;
  height: 100%;
}
.ul-u .li-l:nth-child(3) {
  width: 65%;
}
.img-car {
  width: 300px;
  height: 300px;
}
.p-p1 {
  height: 50%;
  margin-left: 30px;
  font-size: 30px;
  text-align: left;
}
.p-p2 {
  margin-left: 30px;
  width: 100%;
  height: 50%;
}
.sp1 {
  width: 50%;
  height: 100%;
  display: block;
  float: left;
  line-height: 96px;
  text-align: left;
  color: #db698d;
}
.sp2 {
  width: 50%;
  height: 100%;
  font-size: 14px;
  display: block;
  float: left;
}
.i1,
.i2,
.i3 {
  width: 30px;
  height: 32px;
  line-height: 96px;
}
.i1,
.i3 {
  /* border-radius: 45px;
    border: solid rgb(218, 216, 216); */
  padding: 8px;
  font-style: normal;
  font-weight: bold;
}
.i2 {
  width: 15px;
  line-height: 96px;
}

.blank {
  width: 100%;
  height: 80%;
  background-color: #f7f7f7;
  text-align: center;
}
.img-1 {
  margin: 0 30%;
  padding-top: 35%;
  width: 300px;
  height: 300px;
  text-align: center;
}
.p1 {
  font-size: 30px;
  margin: 50px 0;
  line-height: 50px;
}
.p2 {
  font-size: 30px;
  line-height: 60px;
  margin-top: 50px;
  width: 50%;
  /* border-radius:10px; */
  margin: 0 25% 0 25%;
  border: 1px solid #01a84a;
  color: #01a84a;
}
.checkall {
  width: 100%;
  z-index: 100;
  background-color: #f7f7f7;
  position: fixed;
  bottom: 90px;
  height: 100px;
}
.ul-box {
  width: 100%;
  height: 100px;
}
.ul-box li {
  line-height: 100px;
  text-align: center;
  font-size: 20px;
  float: left;
}
.ul-box li:nth-child(1) {
  width: 9%;
}
.ul-box li:nth-child(2) {
  width: 9%;
}
.ul-box li:nth-child(3) {
  width: 32%;
}
.ul-box li .l-p1 {
  font-size: 20px;
  line-height: 100px;
  height: 50px;
}
.ul-box li:nth-child(4) {
  width: 50%;
}

.result {
  width: 100%;
  height: 100px;
  color: #fff;
  font-size: 30px;
  /* border-radius: 50px; */
  background-color: #e20f56;
}
</style>




