<template>
  <div>
    <mt-header fixed title="确认订单">
      <router-link :to="{name:'myshoppingcar'}" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div class="add-ress">
      <h2>配送上门</h2>
      <div class="info-address" v-show="addresfalsge">
        <p>收货人：{{addressInfo.name}}</p>
        <p>收货地址：{{addressInfo.address}} {{addressInfo.addressinfo}}</p>
        <p>联系电话：{{addressInfo.usertel}}</p>
      </div>
      <router-link :to="{name:'address'}">
        <p class="address-in">+选择收货地址</p>
      </router-link>
    </div>
    <div class="goods-info">
      <h2>商品信息</h2>
      <div class="good-ino" v-for="(item,i) in goodstal " :key="i">
        <router-link :to="{name:'goodsdetail',query:{goodsid:item.goodsid,goodsimg:item.goodsimg}}">
          <div class="fen-goods moveIn">
            <img :src="require('../../assets/image/'+item.goodsimg)" alt>
          </div>
        </router-link>
        <div class="title-goods">
          <p class="goods-name">{{item.goodsname}}</p>
          <p class="mong-cou">
            数量 x {{item.buynum}}
            <span class="car-add">￥{{(item.buynum*item.goodsprice).toFixed(2)}}</span>
          </p>
        </div>
      </div>
    </div>
    <div id="goods-footer">
      <div class="shop-add">
        合计:
        <span>￥{{goodsprices.toFixed(2)}}</span>
      </div>
      <span class="shop-car" @click="addGoods">确认</span>
    </div>
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
import { Toast } from "mint-ui";
export default {
  data() {
    return {
      goodstal: [],
      goodsprices: 0,
      addressInfo: {},
      addresfalsge: false
    };
  },
  created() {
    if (!localStorage.username) {
      this.$router.push({ name: "login" });
    } else {
      this.$http
        .get("http://39.97.98.141:3700/vue/searchaddRess", {
          params: {
            username: localStorage.username
          }
        })
        .then(res => {
          if (res.body.result) {
            this.addressInfo = res.body.result;
            this.addresfalsge = true;
          } else {
            this.addresfalsge = false;
          }
        });
      var goodsprices = 0;
      var getgoodsid = localStorage.checkgoods.split(",");
      var getgoods = [];
      this.$http
        .get("http://39.97.98.141:3700/vue/shoppingcar", {
          params: {
            username: localStorage.username
          }
        })
        .then(res => {
          if (res.body.result.length != 0) {
            this.goodstal = res.body.result;
            this.goodstal.forEach(function(item) {
              goodsprices += item.buynum * item.goodsprice;
            });
            this.goodsprices = goodsprices;
            this.goodstal.forEach((item, index) =>
              getgoodsid.forEach(items => {
                if (item.goodsid == items) {
                  getgoods.push(item);
                }
              })
            );
            this.goodstal = getgoods;
          } else {
            this.$router.push({ name: "home" });
          }
        });
    }
  },
  methods: {
    addGoods() {
      if (this.addresfalsge) {
        Toast({
          message: "提交订单成功",
          duration: 500
        });
      } else {
        Toast({
          message: "填写收货地址",
          duration: 500
        });
      }
    }
  },
  computed: {
    ...mapState(["buygoodsnum"])
  }
};
</script>
<style lang="css" scoped>
.add-ress {
  margin-top: 110px;
  font-size: 26px;
  border-bottom: 2px;
}
.add-ress h2 {
  color: #01a84a;
  padding: 30px;
  font-size: 30px;
  line-height: 80px;
  border-bottom: 1px dotted rgb(143, 139, 139);
}
.info-address p {
  line-height: 60px;
  margin-left: 60px;
}
.add-ress .address-in {
  width: 400px;
  margin: 20px auto;
  height: 100px;
  line-height: 100px;
  text-align: center;
  border: 1px dotted rgb(143, 139, 139);
}
.mint-header {
  background: #01a84a;
  color: #fff;
}
.goods-info {
  overflow: hidden;
  padding-bottom: 100px;
}
.good-ino {
  overflow: hidden;
  width: 98%;
  margin: auto;
  margin-top: 10px;
  float: right;
  margin-left: 40px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  line-height: 100px;
}
.goods-info h2 {
  border-top: 1px solid #01a84a;

  color: #000;
  padding: 20px;
  font-size: 30px;
  line-height: 80px;
  border-bottom: 1px solid #d6ddd9;
}
.fen-goods {
  float: left;
  width: 30%;
  height: 220px;
}
.fen-goods img {
  width: 220px;
  height: 220px;
}
.title-goods {
  width: 66%;
  float: right;
}
.goods-name {
  font-size: 28px;
  line-height: 60px;
  margin-left: 10px;
}
.goods-num {
  line-height: 60px;
  font-size: 12px;
  color: rgb(194, 187, 187);
}
.mong-cou {
  margin-top: 45px;
  line-height: 60px;
  font-size: 20px;
  color: orange;
}
.mong-cou .car-add {
  display: inline-block;
  width: 120px;
  line-height: 80px;
  text-align: center;
  margin-left: 200px;
  color: #000;
}
#goods-footer {
  border-top: 2px solid red;
  width: 100%;
  background-color: #fff;
  z-index: 1000;
  height: 100px;
  line-height: 100px;
  bottom: 0px;
  position: fixed;
}
#goods-footer .shop-car {
  border-top: 2px solid red;
  width: 50%;
  text-align: center;
  color: #fff;
  font-size: 16px;
  letter-spacing: 3px;
  background-color: orange;
  float: right;
}
#goods-footer .shop-add {
  border-top: 2px solid red;
  width: 50%;
  text-align: center;
  color: #000;
  font-size: 16px;
  letter-spacing: 3px;
  background-color: #fff;
  float: left;
}
.shop-add span {
  color: orange;
}
</style>




