


import Vue from "vue";
import Vuex from "vuex";
import state from './state'
Vue.use(Vuex);

const store = new Vuex.Store({
    state,
    getters: {

    },
    actions: {

        countDesc({ commit }) {
            commit("countDesc")
        },
        loginBtn({ commit }, { url, user, cb }) {
            Vue.http.get(url, {
                params: { user }
            }).then(res => {
                cb(res.data);
                commit("loginBtn")
            })
        },
        regBtn({ commit }, { url, user1, cb }) {
            Vue.http.post(url,
                user1
            ).then(res => {
                cb(res.body.result);
                commit("regBtn")
            })
        },
        getGoods({ commit }, { url, goodsid, cb }) {
            Vue.http.get(url, {
                params: {
                    goodsid
                }
            }).then(res => {
                commit("getGood", res.body.rsult)
            })
        },
        getGoodsInfo({ commit }, { url, searchs }) {
            Vue.http.get(url, {
                params: {
                    searchs
                }
            }).then(res => {
                commit("getGoodsInfo", res.body.rsult)
            })
        },
        showShoppingNum({ commit }) {
            Vue.http.get("http://39.97.98.141:3700/vue/showshoppingnum", {
                params: {
                    username: localStorage.username
                }
            }).then(res => {
                commit("showShoppingNum", res.body)
            })
        },
        addOneShoppings({ commit }, { url, shopinfo, cb }) {
            Vue.http.get(url, {
                params: {
                    goodsinfo: shopinfo.goodsinfo,
                    username: shopinfo.username
                }
            }).then(res => {
                cb(res.body.result);
                commit("addOneShoppings")
            })
        },
        updateCarInfo({ commit }, { url, totalInfo}){
                Vue.http.get(url,{
                    params:{
                        totalInfo: totalInfo
                    }
                }).then(res=>{
                    commit("updateCarInfo")
                })
            
        },
        delCarInfo({commit},{url,delGoods,cb}){
            if(delGoods.length){
                    Vue.http.get(url,{
                        params:{
                            delGoods: delGoods,
                            username:localStorage.username
                        }
                    }).then(res=>{
                        cb(res)
                        commit("delCarInfo",res)
                    })
            }
           
        }
    }
    ,
    mutations: {
        countDesc(state) {
           
                let dec = setInterval(function () {
                    state.num--;
                    if (state.num <= 0) {
                        clearInterval(dec)
                    }
                }, 1000)
            
        },
        rescNum(state){
            state.num = 3;  
        },
        changeCity(state, city) {
            state.city = city;
        },
        loginBtn(state) { },
        regBtn(state) { },
        updateUsername(state, username) {
            state.username = username
        },
        getGood(state, goodsDetail) {
            state.goodsDetail = goodsDetail
        },
        getGoodsInfo(state, searchGoods) {
            state.searchGoods = searchGoods;
        },
        showShoppingNum(state, buygoodsnum) {
            if(buygoodsnum.length!=0){
                state.flagess=true;
                state.buygoodsnum = buygoodsnum[0].total
            }else{
                state.flagess=false;
                state.buygoodsnum=0
            }
        },
        addOneShoppings(state){
            state.flagess=true;
            state.buygoodsnum+=1;
        },
        clearCars(state){
            state.buygoodsnum=0;   
        },
        toatlCarNum(state,num){
            state.buygoodsnum=num; 
        },
        updateCarInfo(state){},
        delCarInfo(state){}
    }

})


export default store;


